.input {
  border: none;
  border-bottom: 3px solid #2d6b91;
  background: #eeeeee;
  border-radius: 4px;
  box-shadow: 2px 2px 7px 0 rgba(0, 0, 0, 0.2);
  outline: none;
  color: dimgray;
  width: 100%;
  height: 50px;
  font-size: 15px;
  padding-left: 5px;
}

.input:invalid {
  -webkit-animation: justshake 0.3s forwards;
          animation: justshake 0.3s forwards;
  color: #444;
}

input:focus {
  background: #ddd;
}

@-webkit-keyframes justshake {
  25% {
    transform: translateX(5px);
  }
  50% {
    transform: translateX(-5px);
  }
  75% {
    transform: translateX(5px);
  }
  100% {
    transform: translateX-(5px);
  }
}

@keyframes justshake {
  25% {
    transform: translateX(5px);
  }
  50% {
    transform: translateX(-5px);
  }
  75% {
    transform: translateX(5px);
  }
  100% {
    transform: translateX-(5px);
  }
}/*# sourceMappingURL=input.css.map */