button {
    background: #e8e8e8;
    padding: 0.7em 1.7em;
    font-size: 18px;
    border-radius: 0.5em;
    color: #2d6b91;
    border: none;
    transition: all .3s;
    font-family: 'Bebas Neue', cursive;
    cursor: pointer;
    letter-spacing: .5px;
    overflow: hidden;
    display: grid;
    place-items: center;
    padding: 0px;
    height: 40px;
    width: 130px;
   }
   
   button:hover {
    border: 1px solid white;
    color: #f1c00c;
    background-color: white;
    box-shadow: 6px 6px 12px #c5c5c5,
            -6px -6px 12px #ffffff;
   }
   
   button:active {
    box-shadow: 4px 4px 12px #c5c5c5,
                -4px -4px 12px #ffffff;
   }

